* {padding: 0;margin: 0;border: 0;}
*, *:before, *:after {-webkit-box-sizing: border-box;box-sizing: border-box;}
:focus, :active {outline: none;}
a:focus, a:active {outline: none;}
nav, footer, header, aside {display: block;}
html, body {height: 100%;width: 100%;font-size: 100%;line-height: 1;font-size: 14px;-ms-text-size-adjust: 100%;-moz-text-size-adjust: 100%;-webkit-text-size-adjust: 100%;}
input, button, textarea {font-family: inherit;}
input::-ms-clear {display: none;}
button {cursor: pointer;}
button::-moz-focus-inner {padding: 0;border: 0;}
a, a:visited {text-decoration: none; color: inherit;}
a:hover {text-decoration: none;}
ul li {list-style: none;}
img {vertical-align: top;}
h1, h2, h3, h4, h5, h6 {font-size: inherit;font-weight: 400;}

@font-face {
  font-family: "Cyrillic";
  font-style: normal;
  src: url('../public/cyrillic.ttf') format('truetype');
  font-weight: normal;
  font-stretch: 1% 500%;
  color: #0B2349;
}

:root {
  --cyrillic-font: Cyrillic;
  --font-color: linear-gradient(30deg, rgba(40,21,0,0.6898153011204482) 0%, rgba(47,28,0,0.6833727240896359) 32%, rgba(58,31,0,0.658890931372549) 48%, rgba(50,23,0,0.6385547969187676) 73%, rgba(82,56,0,0.7430365896358543) 100%);
}

.App {
  height: 100vh;
  width: 100vw;
}

.bg {
  z-index: -2;
  position: absolute;
  height: 100vh;
  width: 100vw;
  background: rgb(255,211,126);
  background: linear-gradient(130deg, rgba(255,211,126,1) 7%, rgba(245,206,124,1) 57%, rgba(195,162,64,1) 100%);
}

.bg-image {
  z-index: -1;
  position: absolute;
  height: 100vh;
  width: 100vw;
  opacity: 0.5;
  background-image: url('../public/img/paper.jpg');
}