.title {
    text-align: center;
    font-family: var(--cyrillic-font);
    font-size: 70px;
    background: var(--font-color);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.title::first-letter {
    font-size: 100px;
    background: linear-gradient(159deg, rgba(241,73,20,0.7430365896358543) 18%, rgba(222,66,0,0.8170421918767507) 37%, rgba(255,79,0,0.8094231442577031) 50%, rgba(255,37,7,0.8122242647058824) 63%, rgba(255,95,5,0.4) 80%, rgba(255,11,11,0.8370141806722689) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}